*,*::before,*::after{
    box-sizing: border-box;
}

img{
    max-width: 100%;
    height: auto;
}

.classical_glasses h2{
    color: #650300;
    font-family: Noto Sans TC;
    font-size: 48px;
    line-height: 72px;
    font-weight: bold;
    text-align: center;
    padding-top: 80px;
    padding-bottom: 48px;
}

.glasses_category{
    display: flex;
    max-width: 1296px;
    justify-content: space-between;
    padding-bottom: 80px;
}

.category{
    max-width: 416px;
    img{
        height: auto;
        max-width: 100%;
        margin-bottom: 8px;
    }
    p{
        color: #AA0601;
        font-family: Roboto;
        font-size: 48px;
        line-height: 72px;
        font-weight: bold;
        text-align: center;
    }
}

.spectacles_frame{
    background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/index-bg.png);
    background-size: cover;
	background-position: center;
    height: 608px;
    h2{
        color: #FFFFFF;
        font-family: Noto Sans TC;
        font-size: 48px;
        line-height: 72px;
        font-weight: bold;
        text-align: center;
        padding-top: 80px;
        padding-bottom: 48px;
    }
}

.frame_style{
    display: flex;
    justify-content: space-between;
    max-width: 1296px;
}

.style{
    background-color: #FFFFFF;
    img{
        max-width: 100%;
    }
    p{
        color: #AA0601;
        font-family: Roboto;
        font-size: 48px;
        line-height: 72px;
        font-weight: bold;
        text-align: center;
    }
}